*{
  margin: 0px;
  padding: 0px;
}

.img{
width: 15rem;
border-radius: 8px;
border:0;
outline: none;
}

.card{
  margin: 0px 0px 10px 0px;
  position: relative;
  cursor: pointer;
  border: 0;
  outline: none;
}

.btn-recient{
color:white;
font: bold normal normal 15px/25px "Helvetica Neue",helvetica,arial,san-serif;
border: none;
background: rgb(187, 179, 179);
padding: 10px;
border-radius: 20%;
height: 8px;
text-decoration: none;
cursor: pointer;
}


#modal{
  position:absolute;
  width:80vw;
  height:80vh;
  left:10vw;
  top:10vh;
  background:rgba(0,0,0,0.7);  
}    
#close{
  color:#FFFFFF;
  position:absolute;
  top:10px;
  right:10px;
}

